import { forEach } from '@angular/router/src/utils/collection';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
import { locale } from 'moment';
declare var $: any;
@Component({
  providers: [Beyond],
  selector: 'app-enable-vanlynk',
  templateUrl: './enable-vanlynk.component.html',
  styleUrls: ['./enable-vanlynk.component.scss']
})
export class EnableVanlynkComponent implements OnInit {
  name: string = '';
  updateSubscription: FormGroup;
  loginData: any;
  licenseData: any;
  showData: boolean;
  Data: any;
  eData: any;
  prozDetailsData: any = [];
  enableVanlynkLicenses: any;
  numberOfStaff: any;
  update: boolean;
  employeeList: any;
  showEmployeeData: boolean;
  searchStatus: any;
  spinner: any;
  employeeData: any = [];
  status: boolean;
  employeeData1: any;
  checkboxStatus: boolean;
  totalStaffData: any;
  activeLoginData: any;
  data: any;
  counter: number = 0;
  checked: boolean;
  displayFileModal: string;
  checkArray: any = []
  modalDataHolder: any = {};
  totalFreeLicenses: any = 0
  deactivationLoginSelectedCounter: any = 0
  activeDeactiveLabel: any = 'Deactivate'
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    private formBuilder: FormBuilder,
    public toastr: ToastrService,
  ) {

  }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.getSubscriptions()
    this.listStaffs()
    this.listStaffFreeUsedProSubscriptions()
    this.listTotalStaffFreeUsedProSubscriptions()
    this.listActiveLogin()
    this.updateSubscription = this.formBuilder.group({
      vanylinkStatus: ['Enable', Validators.required],
      noOfLicenses: ['', Validators.required],

    })
  }
  updateButton() {
    $("#confirmation").modal("show");
  }
  confirmationPopup() {
 
    if (this.updateSubscription.value.noOfLicenses >= this.licenseData.numStaff) {
      let updateObj = {
        "proCode": this.loginData.loginCode,
        "vanylinkStatus": "Enable",
        "noOfLicenses": '' + this.updateSubscription.value.noOfLicenses,
        "modifiedBy": this.loginData.emailId,
        "modifiedOn": new Date(),
        "date": new Date().toUTCString()
      }
      //console.log("ADD OBJ" + JSON.stringify(addObj))
      this.http.post(Global.url + '/updateLicenseVanyLinkSubscription', updateObj)
        .subscribe(data => {
          var employeeData = data.json();
          this.eData = employeeData.response;
          console.log("rolesData" + JSON.stringify(employeeData))
          // $("#confirmation").modal("hide");
          this.updateVanlynk(updateObj)
          // this.router.navigate(['/proz/listInventory'])

        }, error => {
          //console.log(error);// Error getting the data
        });
    }
    else {
      alert("you need to remove " + (this.activeLoginData.ActiveLogins - this.updateSubscription.value.noOfLicenses) + " active Logins before you reduce the Licenses")
    }
  }
  onCancel(){
    $("#confirmation").modal("hide");
  }

  updateVanlynk(OBJ) {
    console.log("Update 22::")
    //console.log(OBJ.date)
    let addObj = {
      "proCode": this.loginData.loginCode,
      "vanylinkStatus": 'Enable',
      "noOfLicenses": '' + this.updateSubscription.value.noOfLicenses,
      "modifiedBy": this.loginData.emailId,
      "date": '' + OBJ.date,

    }
    //console.log("ADD OBJ" + JSON.stringify(addObj))
    this.http.post(Global.url + '/updateVanyLinkSubscription', addObj)
      .subscribe(data => {
        var employeeData = data.json();
        console.log("rolesData" + JSON.stringify(employeeData))
        this.updateLogin(OBJ)
      }, error => {
        //console.log(error);// Error getting the data
      });
  }
  updateLogin(OBJ) {

    console.log("Update 333::")
    let addObj = {
      "proCode": this.loginData.loginCode,
      "vanylinkStatus": this.updateSubscription.value.vanylinkStatus,
      "noOfLicenses": '' + this.updateSubscription.value.noOfLicenses,
      "modifiedBy": this.loginData.emailId,
      "date": '' + OBJ.date,
    }
    //console.log("ADD OBJ" + JSON.stringify(addObj))

    this.http.post(Global.url + '/updateLoginVanyLinkSubscription', addObj)
      .subscribe(data => {
        var employeeData = data.json();
        //console.log("rolesData" + JSON.stringify(employeeData))
        this.toastr.success('Login Subscription Added Successfuly...');
        //console.log(this.updateSubscription.value.vanylinkStatus)
        if (this.updateSubscription.value.vanylinkStatus == "Enable") {
          var vanlynk = "Y"
        } else {
          var vanlynk = "N"
        }
        this.loginData.vanlynk = vanlynk
        localStorage.setItem("login", JSON.stringify(this.loginData));
          $("#confirmation").modal("hide");
          location.reload()
        // this.router.navigate(['/subscription/confirmVanlynk'])
        //console.log('role cahnged' + JSON.stringify(this.loginData))
      }, error => {
        //console.log(error);// Error getting the data
      });
  }
  getSubscriptions() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getVanylinkLicence/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozDetailsData = response.response;
          console.log("getsubscriptions", JSON.stringify(this.prozDetailsData))
          console.log("noOflicenses", JSON.stringify(this.prozDetailsData[0].noOfLicenses))
          // if (this.prozDetailsData[0].vanylinkStatus == "Disable") {
          //   this.updateSubscription.get('vanylinkStatus').setValue('Disable')
          // } else {
          //   this.updateSubscription.get('vanylinkStatus').setValue('Enable')
          // }
          this.updateSubscription.get('noOfLicenses').setValue(this.prozDetailsData[this.prozDetailsData.length - 1].noOfLicenses)
          return false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }


  /* Get Active Staff Free Used Pro Subscriptions */
  listStaffFreeUsedProSubscriptions() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getActiveStaff/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          console.log("Active Staff licenses free,used" + JSON.stringify(response))
          this.licenseData = response.response[0];
          this.showData = true;
        }
        else {
          this.showData = false;
          this.toastr.warning('Failed to get Vanlynk licenses');
        }
      }, error => {
        console.log("No Vanlynk Licenses");
      })
  }
  /* /Get Active Staff Free Used Pro Subscriptions */

  /* Get Total Staff Free Used Pro Subscriptions */
  listTotalStaffFreeUsedProSubscriptions() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getTotalStaff/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          console.log("Total Staff licenses free,used" + JSON.stringify(response))
          this.totalStaffData = response.response[0];
          this.showData = true;
        }
        else {
          this.showData = false;
          this.toastr.warning('Failed to get Vanlynk licenses');
        }
      }, error => {
        console.log("Unable to connect to server");
      })
  }
  /* /Get Total Staff Free Used Pro Subscriptions */

  /* Get Total Staff Free Used Pro Subscriptions */
  listActiveLogin() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getActiveLogin/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          console.log("Total Staff licenses free,used" + JSON.stringify(response))
          this.activeLoginData = response.response[0];
          this.totalFreeLicenses = parseInt(this.prozDetailsData[0].noOfLicenses) - parseInt(this.activeLoginData.ActiveLogins)
          this.showData = true;
        }
        else {
          this.showData = false;
          this.toastr.warning('Failed to get Vanlynk licenses');
        }
      }, error => {
        console.log("Unable to connect to server");
      })
  }
  /* /Get Total Staff Free Used Pro Subscriptions */


  /*List of Staff*/

  listStaffs() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listEmployee/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.employeeData = response.response;
          console.log("Employee Data" + JSON.stringify(this.employeeData))
          this.employeeData.forEach((e) => {
            e.checked = false
            e.statusCheck = false
            if (e.status == 'ACTIVE') {
              e.statusCheck = false
              e.checked = true
            } else {
              e.statusCheck = true
              e.checked = false
            }
          })
          this.showData = true;
        }

        else {
          this.showData = false;
        }
      }, error => {
        console.log("Unable to connect to server");
      })
  }
  cancel() {
    this.router.navigate(['/proz-dashboard/prozDashboard', this.loginData.loginCode])
  }

  sendDataToModal(data) {
    console.log(data)
    this.modalDataHolder = data
    data.checked == false ? this.activeDeactiveLabel = 'Deactivate' : this.activeDeactiveLabel = 'Activate'
    data.checked == false ? data.status = "INACTIVE" : data.status = "ACTIVE"
  }

  dismissActiveLoginFunction() {
    console.log(this.modalDataHolder)
    var dataHolder;
    this.employeeData.forEach((e) => {
      if (e.id == this.modalDataHolder.id) {
        e.checked = !e.checked
        dataHolder = e
      }
    })
    // if (this.modalDataHolder.checked == dataHolder.checked) {
    //   this.totalFreeLicenses++
    //   this.deactivationLoginSelectedCounter++
    // } else {
    //   this.totalFreeLicenses--
    //   this.deactivationLoginSelectedCounter--
    // }
  }

  statusToggle() {
    console.log(this.employeeData)
    if (this.modalDataHolder.checked == false) {
      this.totalFreeLicenses++
      this.deactivationLoginSelectedCounter++
    } else {
      this.totalFreeLicenses--
      this.deactivationLoginSelectedCounter--
    }
  }


  apply() {
    console.log(this.checkArray)
  }


  check() {
    //alert("You are deactivating the user, Are you sure you want to continue")
    // this.activeLoginData.status = "INACTIVE";
    // data.status == 'INACTIVE';

    this.counter--; // counter is the varible    
    if (this.counter % 2 == 1) {
      this.checked = false; //checked is the variable
    }
    else {
      this.checked = true;
    }
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
