import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProzSubscriptionLayoutComponent } from './proz-subscription-layout/proz-subscription-layout.component';
import { EnableVanlynkComponent } from './enable-vanlynk/enable-vanlynk.component';
import { ProzListSubscriptionsComponent } from './proz-list-subscriptions/proz-list-subscriptions.component';
import { ProzSubscriptionsComponent } from './proz-subscriptions/proz-subscriptions.component';
import { VanLynkConfimationComponent } from './van-lynk-confimation/van-lynk-confimation.component';
import { ProzSubscriptionPaymentHistoryComponent } from './proz-subscription-payment-history/proz-subscription-payment-history.component';
const routes: Routes = [{
  path: '', component: ProzSubscriptionLayoutComponent,

  children: [
    {
      path: 'prozsubscription',
      component: ProzSubscriptionsComponent
    },
    {
      path: 'listProSubscriptions',
      component: ProzListSubscriptionsComponent
    },
    {
      path: 'enableVanlynkData',
      component: EnableVanlynkComponent
    },
    {
      path: 'confirmVanlynk',
      component: VanLynkConfimationComponent
    },
    {
      path: 'paymentHistory/:proCode',
      component: ProzSubscriptionPaymentHistoryComponent
    },

  ]
}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProzSubscriptionRoutingModule { }
