import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../utilities/beyond';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-van-lynk-confimation',
  templateUrl: './van-lynk-confimation.component.html',
  styleUrls: ['./van-lynk-confimation.component.scss']
})
export class VanLynkConfimationComponent implements OnInit {
  loginData: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    private formBuilder: FormBuilder,
    public toastr: ToastrService,
  ) {

  }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"))

  }
  home() {
    localStorage.setItem('proServiceAreas', null);
    this.router.navigate(['/proz-dashboard/prozDashboard', this.loginData.loginCode])
  }
  list() {
    this.router.navigate(['/subscription/listProSubscriptions'])
  }
}

