  /* /Get Staff Free Used Pro Subscriptions */
  /* Get Total Staff Free Used Pro Subscriptions */
  listTotalStaffFreeUsedProSubscriptions(proCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getTotalStaff/' + proCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          console.log("Total Staff licenses free,used" + JSON.stringify(response))
          this.totalStaffData = response.response[0];
          this.getSubscriptions()
          this.showLicenseData = true;
          this.showData = true;
          this.getSubscriptions()
        }
        else {
          this.showLicenseData = false;
          this.showData = false;
          this.toastr.warning('Failed to get Vanlynk licenses');
        }
      }, error => {
        console.log("Unable to connect to server");
      })
  }
  /* /Get Total Staff Free Used Pro Subscriptions */

  /* Get Total Staff Free Used Pro Subscriptions */
  listActiveLogin() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getActiveLogin/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          console.log("Total Staff licenses free,used" + JSON.stringify(response))
          this.activeLoginData = response.response[0];
          this.activeLoginDataStatus = true;
        }
        else {
          this.activeLoginDataStatus = false;
          this.toastr.warning('Failed to get Vanlynk licenses');
        }
      }, error => {
        console.log("Unable to connect to server");
      })
  }
  /* /Get Total Staff Free Used Pro Subscriptions */

